.main_info{
    margin-bottom: 30px;
    border-radius: 8px;
    padding: 0px 30px 30px;
    background-color: white;
}
.profile-img{
    text-align: center;
    width: 90px;
    margin: 0 auto;
}
.profile-img img{
    width: 80px;
    border-radius: 50%;
    border: 3px solid white;
    object-fit: cover;
}
.form-label{
    font-weight: 700;
    margin: 0px 0px 7px 3px;
    white-space: nowrap;
    text-transform: capitalize
}
.heading p{
    font-size: 1.1rem;
    font-weight: 600;
}
.heading button{
    border: none;
    font-size: 15px;
    font-weight: 700;
    border-radius: 6px;
    letter-spacing: 0.3px;
    padding: 5px 15px;
    text-transform: capitalize;
    color: white;
    background: #027782
}