
.user-form-card{
    padding: 30px;
    border-radius: 8px;
    margin-bottom: 20px;
    background: white;
    border: 1px solid #e8e8e8;
}
.sign-up-cont{
    border-radius: 8px;
    margin-bottom: 20px;
    background: white;
    border: 1px solid #e8e8e8;
}
.wlcm-text{
    color: #027782;
    margin-bottom: 10px;
    font-size: 28px;
    line-height: 36px;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
}
 .form-control{
    width: 100%;
    height: 45px;
    border-radius: 6px;
    padding: 0px 20px 2px;
    background: #f5f5f8;
    border: 1px solid #e8e8e8;
}
.user-login-button{
    width: 100%;
    height: 45px;
    font-size: 14px;
    font-weight: 700;
    line-height: 45px;
    border-radius: 8px;
    letter-spacing: 0.3px;
    margin-bottom: 8px;
    text-align: center;
    text-transform: uppercase;
    color: white;
    outline: none;
    border: none;
    background: #027782;
}
.user-login-button:focus{
    outline: none;
}
.form-control:focus{
   box-shadow: none !important;
   background-color: #f2f2f4 !important;
   border: 1px solid #027782;
}
.sign-up-cont span, .reset{
    cursor: pointer;
}