:root{
	--mainGreen :#027782;
	--mainYellow :#ffcb08;
	--mainDark: #242325;
  	--mainWhite: #fff;
  	--mainGrey: #eeeeee;
}
.navbar_menu{
	font-size: 1.6rem !important;
	color: #027782;
}
.navbar-brand{
	width: 15%;
	text-align: center;
}
.navbar-toggler{
	outline: none;
	border:none;
}
.navbar-toggler:focus{
	outline: none;
	border:none;
}
.nav-item{
	padding-right: 20px;
}
.nav-link{
	font-weight: 600;
	color: black !important;
}
.nav-active{
	color: var(--mainGreen) !important;
}
.nav-icon{
    font-size: 1.7rem;
    color: var(--mainGreen) !important;
}
.nav-register{
    cursor: pointer;
}