.card-img-top img{
    width: 100%;
    height: 280px;
    object-fit: cover;
    position: relative;
}
.blog-btn{
    width: 120px;
    border: 0.1rem solid #027782 !important;
    outline: none;
    background-color: #027782;
    color: white;
    padding: 6px;
    transition: all 0.2s linear;
}
.blog-btn:hover{
    background-color: transparent;
    color: black;
    border: 0.1rem solid black !important;
}
.date{
    position: absolute;
    width: 120px;
    background-color: rgba(0, 0, 0, 0.4);
    color: white;
    text-align: center;
    letter-spacing: 1px;
    padding: 10px;
}