

.product_img_container{
	text-align: center;
}
.product_img_container img{
	height: 250px;
	width: 268px;
}
.add_to_cart_btn{
	border: none;
	outline: none;
	text-align: center;
	border-radius: 50px;
	background-color: #ffc107;
	color: white;
	font-size: 13px;
	padding: 6px 10px;
} 
h6{
	line-height: 14px;
	padding-bottom: 7px;
	letter-spacing: 1px;
}
.shop_product_img_container{
	background-color: rgb(235, 235, 235);
    text-align: center !important;

}
.owl-carousel .owl-item img{
	width: 220px !important;
	margin: 0 auto;
}
.shop_product_img_container:hover img{
	transform: scale(1.1);
	transition: all 0.5s ease;
}
.shop_product_img_container{
	position: relative;
	overflow: hidden;
}
.shop_product_img_container .fa-shopping-cart{
	position: absolute;
	top: 10%;
	font-size: 1.3rem;
	right: 12px;
	background-color: white;
	transform: translateY(-200%);
	box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
	padding: 10px;
	transition: all 0.4s ease;
}
.fa-search-plus{
	position: absolute;
	font-size: 1.3rem;
	top: 27%;
	right: 12px;
	background-color: white;
	box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
	color: black !important;
	padding: 10px;
	transform: translateY(-300%);
	transition: all 0.4s ease;
}
.shop_product_img_container:hover .fa-search-plus{
	transform: translateY(0%);
}
.shop_product_img_container:hover .fa-shopping-cart{
	transform: translateY(0%);
}

.product_rows{
	overflow-y: hidden;
	overflow-x: scroll;
}
