@import url('https://fonts.googleapis.com/css2?family=Work+Sans&display=swap');

.cart_box{
    min-height: 80vh;
    background-color: white;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
}
.cart_info_box{
    min-height: 60vh !important;
    background-color: white;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
}
.cart_img_container{
    width: 200px;
    height: 210px;
    background-color: rgb(235, 235, 235);
    padding: 20px;
    margin: 10px 0px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
    text-align: center;

}
.cart_img_container img{
    width: 140px;
    height: 150px;
}
.empty-cart{
    text-align: center;
    background-color: white
}
.empty-cart i{
    font-size: 8rem;
    color: rgb(238, 238, 238);;
}
.empty-cart h6{
    text-transform: uppercase;
    color: black;
    margin-top: 0.6rem;
    font-size: 2rem;
    letter-spacing: 2px;
    font-family: 'Work Sans', sans-serif;
}
.empty-cart p{
    color: rgb(196, 194, 194);
    margin-top: 1.3rem;
    margin-bottom: 2rem;
}
.empty-cart .cart-btn{
    color: white;
    padding: 0.7rem 1.4rem;
    background-color: #027782;
    outline: none;
    border: none;
}
.empty-cart img{
    height: 150px;
    width: 100%;
    object-fit: contain;
}