@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');

#services{
	/* background-image:linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('https://demo2.wpopal.com/pharmacy/wp-content/uploads/revslider/pharmacy_slider/bg-slider2.jpg'); */
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	background-attachment: fixed;
	background-color: white;
}
.service-text{
	font-size: 3rem;
	color: #ffcb08;
	letter-spacing: 1px;
}
.service-icon{
	display: inline-block;
	width: 50px;
	height:50px;
	/* border:0.1rem solid  #027782; */
	color:  #027782;
	/* transform: rotate(45deg); */
	margin-bottom: 1.5rem;
	margin-top: 1.5rem;
	transition: all 0.5s ease-in-out;

}
.service-icon i{
	line-height: 50px;
	/* transform: rotate(-45deg); */
	font-size: 3rem;
	transition: all 0.3s ease;
}
#services p{
	color: rgb(156, 156, 156);
	letter-spacing: 0.7px;
}
#services .col-10{
	padding: 20px 0px;
	margin-right: 12px;
}
#services .col-10:hover{
	box-shadow: 0 3px 16px 0 rgb(0 0 0 / 11%);
	transition: all 0.3s ease;
}
#services .col-10:hover i{
	transform: scale(1.5);
}
#services h3{
    color: #027782 !important;
	letter-spacing: 2px;
	font-family: 'Roboto Slab', serif
}