@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');
.newsletter{
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("https://images.unsplash.com/photo-1573883431205-98b5f10aaedb?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80");
    background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	background-position: center;
}
.newsletter .head{
    color: white;
    text-transform: uppercase;
    font-family: 'Lora', serif;
    font-size: 2rem;
    letter-spacing: 2px;
}
.newsletter p{
    color: rgba(255,255,255,0.7);
    word-wrap: break-word;
    margin-top: -10px;
}
.newsletter input{
    width:40rem;
    background-color: transparent;
    color: white !important;
    outline: none !important;
    border: none;
    border-bottom: 2px solid white;
    margin-top: 1rem;
    padding-bottom: 10px;
}
.newsletter button{
    background-color: white;
    color: black;
    padding: 7px 50px;
    outline: none !important;
    border: none !important;
    text-transform: uppercase;
    font-family: 'Lora';
    font-weight: bold;
    letter-spacing: 1px;
}

@media only screen and (max-width : 768px){
    .newsletter input{
        width: 20rem;
    }
}