.dash-side, .dash-content{
    min-height: 100vh;
}
.bg-green{
    background-color: #027782;
}
.dash .nav-item{
    margin-bottom: 7px;
}
.dash .nav-link{
    margin-left: 15px;
    color: white;
}
.dash .nav-item:hover{
    background-color: #ffc107;
    transition: all 0.3s linear;
}
.dash .nav-item .nav-link{
    color: white !important;
}

.bg-gray{
    background-color: rgb(243, 243, 243);
    min-height: calc(100vh - 76.36px);
}
.dash-sm .nav-item{
    margin-bottom: 2px;
    text-align: center;
}
.admin-nav-icon{
    font-size: 1.3rem;
    
}
.admin-nav-icon i{
    padding-bottom: 0;
    color: white;
}

.active{
    background-color: #ffc107;
}