.blog_info{
    background-color: white;
    /* box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3); */
}
.blog_info_img_container{
    text-align: center;
}
.blog_info_img_container img{
    object-fit: cover;
    height: 60vh;
    width: 94%;
}
.blog-content > h4{
    font-size: 2rem;
    text-transform: capitalize;
    letter-spacing: 2px;
    margin: 1.3rem 0;
}
.blog-content p{
    word-wrap: break-word;
    letter-spacing: 1px;
    font-family: 'Muli', 'sans-serif';
}