:root{
	--mainGreen :#027782;
	--mainYellow :#ffcb08;
	--mainDark: #242325;
  	--mainWhite: #fff;
  	--mainGrey: #eeeeee;
}
.title-main{
    color: #027782;
    color: var(--mainGreen);
    text-transform: uppercase !important;
    font-size: 3.5rem;
}
.blockquote p{
    color: #027782;
    color: var(--mainGreen);
    border-left: 0.3rem solid #ffcb08;
    border-left: 0.3rem solid var(--mainYellow);
}
.blockquote-footer{
    color: #027782;
    color: var(--mainGreen);
}
.main-text{
    font-size: 16px;
}
.img-container{
	position: relative;
}
.img-container::before{
	content: " ";
	position: absolute;
	top: -1.4rem;
	left: 1.6rem;
	width: 100%;
	height: 100%;
	outline: 0.5rem solid #027782;
	z-index: -1;
	transition: all 0.7s linear;
}
.img-container:hover:before{
	top: 0;
	left: 0;
}
.footer-para{
    font-size: 15px;
}
.text-green{
	color: #027782;
}
.title-underline{
	height: 4px;
	width:150px;
	background-color: #ffcb08
}
.copyright{
    color: #027782;
    font-size: 15px;
}
.card-img-top img{
    width: 100%;
    height: 280px;
    object-fit: cover;
    position: relative;
}
.blog-btn{
    width: 120px;
    border: 0.1rem solid #027782 !important;
    outline: none;
    background-color: #027782;
    color: white;
    padding: 6px;
    transition: all 0.2s linear;
}
.blog-btn:hover{
    background-color: transparent;
    color: black;
    border: 0.1rem solid black !important;
}
.date{
    position: absolute;
    width: 120px;
    background-color: rgba(0, 0, 0, 0.4);
    color: white;
    text-align: center;
    letter-spacing: 1px;
    padding: 10px;
}


.product_img_container{
	text-align: center;
}
.product_img_container img{
	height: 250px;
	width: 268px;
}
.add_to_cart_btn{
	border: none;
	outline: none;
	text-align: center;
	border-radius: 50px;
	background-color: #ffc107;
	color: white;
	font-size: 13px;
	padding: 6px 10px;
} 
h6{
	line-height: 14px;
	padding-bottom: 7px;
	letter-spacing: 1px;
}
.shop_product_img_container{
	background-color: rgb(235, 235, 235);
    text-align: center !important;

}
.owl-carousel .owl-item img{
	width: 220px !important;
	margin: 0 auto;
}
.shop_product_img_container:hover img{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	transition: all 0.5s ease;
}
.shop_product_img_container{
	position: relative;
	overflow: hidden;
}
.shop_product_img_container .fa-shopping-cart{
	position: absolute;
	top: 10%;
	font-size: 1.3rem;
	right: 12px;
	background-color: white;
	-webkit-transform: translateY(-200%);
	        transform: translateY(-200%);
	box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
	padding: 10px;
	transition: all 0.4s ease;
}
.fa-search-plus{
	position: absolute;
	font-size: 1.3rem;
	top: 27%;
	right: 12px;
	background-color: white;
	box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
	color: black !important;
	padding: 10px;
	-webkit-transform: translateY(-300%);
	        transform: translateY(-300%);
	transition: all 0.4s ease;
}
.shop_product_img_container:hover .fa-search-plus{
	-webkit-transform: translateY(0%);
	        transform: translateY(0%);
}
.shop_product_img_container:hover .fa-shopping-cart{
	-webkit-transform: translateY(0%);
	        transform: translateY(0%);
}

.product_rows{
	overflow-y: hidden;
	overflow-x: scroll;
}

.carousel_image, .carousel_image1{
    height: 70vh;
	background-repeat: no-repeat;
	background-position: center;
    background-size: cover;
    background-attachment: fixed;
}
.carousel_image{
    background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.4)), url('https://demo2wpopal.b-cdn.net/pharmacy/wp-content/uploads/revslider/pharmacy_slider/slide1.png');
}
.carousel_image1{
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url('https://images.unsplash.com/photo-1477332552946-cfb384aeaf1c?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80');
}
.height-max{
    height: 75vh;
}
.carousel_content h4{
    font-size: 52px;
    margin: 4px;
    letter-spacing: 2px;
    font-weight: 700;
}
.carousel_content p{
    margin: 4px;
    letter-spacing: 4px;
}
.carousel_content button{
    margin-top: 20px;
    text-align: center;
    border: 2px solid rgba(247, 247, 247, 0.3);
    color: white;
    border-radius: 30px;
    width: 10rem;
    padding: 5px;
    background-color: transparent;
    outline: none;
}

@media (max-width: 768px) { 
    .carousel_image, .carousel_image1{
        background-size: cover;
        background-position: left;
    }
    .carousel_content h4{
        font-size: 36px;
        margin: 4px;
        letter-spacing: 2px;
    }
    .height-max{
        text-align: center;
    }
}
.newsletter{
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("https://images.unsplash.com/photo-1573883431205-98b5f10aaedb?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80");
    background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	background-position: center;
}
.newsletter .head{
    color: white;
    text-transform: uppercase;
    font-family: 'Lora', serif;
    font-size: 2rem;
    letter-spacing: 2px;
}
.newsletter p{
    color: rgba(255,255,255,0.7);
    word-wrap: break-word;
    margin-top: -10px;
}
.newsletter input{
    width:40rem;
    background-color: transparent;
    color: white !important;
    outline: none !important;
    border: none;
    border-bottom: 2px solid white;
    margin-top: 1rem;
    padding-bottom: 10px;
}
.newsletter button{
    background-color: white;
    color: black;
    padding: 7px 50px;
    outline: none !important;
    border: none !important;
    text-transform: uppercase;
    font-family: 'Lora';
    font-weight: bold;
    letter-spacing: 1px;
}

@media only screen and (max-width : 768px){
    .newsletter input{
        width: 20rem;
    }
}
