.blog_content .card{
    outline: none !important;
    border: none !important;
}
.blog_content .card-body h4{
    color: #027782;
}
.blog_content .card-body p{
    color: rgb(15, 13, 13);
    letter-spacing: 1px;
}
.new-blog{
    width: auto;
}
.new-blog img{
    width: 300px;
    height: 190px;
    object-fit: cover;
}
.new-blog p{
    font-size: 15px;
    padding-top: 4px;
    letter-spacing: 1px;
}
.ant-pagination-item-active{
    background-color: #027782 !important;
    color: white;
    border-color: #027782;
}
.blog_read_more{
    color: #027782;
}
.card-body .blog_desc{
    color: gray !important;
}