@import url('https://fonts.googleapis.com/css2?family=Work+Sans&display=swap');

#wishlist{
    background-color: white;
}
.product_img_wish_container{
    background-color: rgb(230, 228, 228);
    /* box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3); */
    text-align: center;
    height: 300px;
    line-height: 300px;
    margin-bottom: 12px;
}   
.product_img_wish_container img{
    width: 100%;  
    max-height: 200px; 
    object-fit: contain;
}
.wishlist_content .title{
    font-size: 1.2rem;
    float: left;
}
.wishlist_content .amount{
    font-size: 1.2rem;
    float: right;
}
.empty-wish{
    width: 100%;
    text-align: center;
    color: rgb(238, 238, 238);
}
.empty-wish i{
    font-size: 8rem;
}
.empty-wish h6{
    text-transform: uppercase;
    color: black;
    margin-top: 1.8rem;
    font-size: 2rem;
    letter-spacing: 2px;
    font-family: 'Work Sans', sans-serif;
}
.empty-wish p{
    color: rgb(196, 194, 194);
    margin-top: 1.3rem;
    margin-bottom: 2rem;
}
.empty-wish .wish-btn{
    color: white;
    padding: 0.7rem 1.4rem;
    background-color: #027782;
}