.carousel_image, .carousel_image1{
    height: 70vh;
	background-repeat: no-repeat;
	background-position: center;
    background-size: cover;
    background-attachment: fixed;
}
.carousel_image{
    background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.4)), url('https://demo2wpopal.b-cdn.net/pharmacy/wp-content/uploads/revslider/pharmacy_slider/slide1.png');
}
.carousel_image1{
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url('https://images.unsplash.com/photo-1477332552946-cfb384aeaf1c?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80');
}
.height-max{
    height: 75vh;
}
.carousel_content h4{
    font-size: 52px;
    margin: 4px;
    letter-spacing: 2px;
    font-weight: 700;
}
.carousel_content p{
    margin: 4px;
    letter-spacing: 4px;
}
.carousel_content button{
    margin-top: 20px;
    text-align: center;
    border: 2px solid rgba(247, 247, 247, 0.3);
    color: white;
    border-radius: 30px;
    width: 10rem;
    padding: 5px;
    background-color: transparent;
    outline: none;
}

@media (max-width: 768px) { 
    .carousel_image, .carousel_image1{
        background-size: cover;
        background-position: left;
    }
    .carousel_content h4{
        font-size: 36px;
        margin: 4px;
        letter-spacing: 2px;
    }
    .height-max{
        text-align: center;
    }
}