.add-to-cart-btn{
    outline: none;
    background-color: #ffc107;
    color: white;
    border: none;
    width: 120px;
    padding: 10px;
    border-radius: 30px;
    cursor: pointer;
    margin-left: -10px;
}
.disabled-btn{
    background-color: black !important;
}
.add-to-wishlist-btn{
    outline: none;
    background-color: #f11a1a;
    color: white;
    border: none;
    width: 145px;
    padding: 10px;
    border-radius: 30px;
}
.product_title{
    font-size: 35px;
}
.product-sub{
    font-weight: 700;
}
.product-sub span{
    font-weight: 400;
}
.fa-star{
    color: red;
    font-size: 20px;
}
.product_desc h5{
    text-decoration: underline;
}
