.products_list_container{
    min-height: 760px;
    background-color: white;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
}
.single_product .img_container{
    width: 200px;
    background-color: rgb(226, 226, 226);
    padding: 20px;
    height: 170px;
}
.single_product img{
    width: 120px;
    object-fit: contain;
}
.shop_product_container:hover{
    box-shadow: 0 3px 16px 0 rgb(0 0 0 / 11%);
    transition: 0.2s all ease;
}
.shop_product_img_container{
    background-color: white;
    height: 270px;
    position: relative;
}
.shop_product_img_container .fa-heart{
    position: absolute;
    top: 10%;
    right: 8%;
    font-size: 1.4rem;
    color: rgb(219, 217, 217);
}
.fa-star{
    font-size: 0.8rem !important;
}
.shop_gif_loader{
    min-height: 650px;
}

/*MOBILE VIEW CSS*/
.mobile_product_card{
    height: 220px;
}
.p_img_cont{
    text-align: center;
    height: 200px;
    line-height: 200px;
}
.p_img_cont img{
    width: 100%;
    max-height: 190px;
}
.info_head .fa-heart{
    color: rgb(199, 199, 199);
    font-size: 1.2rem;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.1);
    padding: 6px;
    border-radius: 50%;
    margin-top: -10px;
}