.main-body{
    background-color: white;
}
.map{
    height: 80vh;
    width: 100%;
}
.head-underline{
    width: 70px;
    height: 3px;
    background-color: #027782;
}
.icon-con{
    font-size: 2rem;
    color: #027782;
}
.message-form input{
    outline: none;
    border: none;
    width: 100%;
    height: 40px;
    background-color: rgb(238, 238, 238);
    padding-left: 20px;
}
.message-form-sub input{
    outline: none;
    border: none;
    width: 100%;
    height: 40px;
    background-color: rgb(238, 238, 238);
    padding-left: 20px;
}
.message-form-msg textarea{
    outline: none;
    border: none;
    width: 100%;
    height: 250px;
    background-color: rgb(238, 238, 238);
    padding-left: 20px;
}
.msg-btn{
    outline: none;
    border: 1px solid #027782;
    color: #027782;
    padding: 8px 30px;
    transition: all 0.3s linear;
    background-color: white;
}
.msg-btn:hover{
    background-color: #027782;
    color: white;
}
.address-btn{
    outline: none;
    border: 1px solid #027782;
    color: #027782;
    padding: 8px 30px;
    transition: all 0.3s linear;
    background-color: transparent;
}
.address-btn:hover{
    background-color: #027782;
    color: white;
}