@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');

#testimonials{
	/* background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("https://demo2.wpopal.com/pharmacy/wp-content/uploads/revslider/pharmacy_slider/slide1.png");
	background-repeat: no-repeat; */
	background-size: cover;
	background-attachment: fixed;
	background-position: center;
	background-color: white;
}
.author_img img{
	margin-top: 10px;
	border-radius: 50%;
	width: 80px !important;
	height: 80px !important;
}
.testi-container{
	box-shadow: 0 3px 16px 0 rgb(0 0 0 / 11%);
	text-align: center;
	position: relative;
}

.testi-container .author{
	text-transform: uppercase;
	font-size: 1.2rem;
	letter-spacing: 1.2px;
	margin-top: 1rem;
}
.testi-container .desc{
	font-size: 0.8rem;
	padding: 0px 30px;
	letter-spacing: 1.2px;
	font-family: 'Raleway', sans-serif;
	color: rgb(173, 173, 173);
}
.testi-container .star .fa-star{
	color: #027782;
	font-size: 1.3rem !important;
	margin-left: 6px;
}