.order_placed_cont{
    min-height: 70vh;
    background-color: white;
    box-shadow: 0 3px 16px 0 rgb(0 0 0 / 11%);
}
.check_cont i{
    font-size: 3.5rem;
}
.success_text{
    font-size: 1.6rem;
    letter-spacing: 2px;
}
.order_number_text{
    text-align: center;
    border-top: 0.2rem solid rgb(241, 241, 241);
    border-bottom:0.2rem solid rgb(241, 241, 241) ;
    padding: 20px 0;
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 1.1rem;
    letter-spacing: 1px;
}
.email_text{
    text-align: center;
    font-size: 0.8rem;
}
.order_total p{
    font-size: 1.2rem;
    letter-spacing: 1px;
    text-transform: uppercase;
}
.pay_meth p{
    font-size: 0.8rem;
    letter-spacing: 1px;
    margin-top: -10px;
}
.success_buttons .btn-first{
    background-color: white;
    outline: none;
    border: 0.1rem solid black;
    padding: 0.4rem 1.5rem;
    letter-spacing: 1px;
}
.success_buttons .btn-second{
    background-color: #28A745;
    outline: none;
    border: 0.1rem solid #28A745;
    padding: 0.4rem 1.5rem;
    letter-spacing: 1px;
    color: white;
}
.delivery_loader img{
    width: 40%;
}
.delivery_loader_text{
    margin-top: -20px;
    font-size: 1rem;
    color: rgb(156, 156, 156);
    letter-spacing: 1.2px;
}

@media (max-width: 768px) { 
    .delivery_loader img{
        width: 80%;
    }
    .check_cont i{
        font-size: 2.7rem;
    }
    .success_text{
        font-size: 1.1rem;
    }
    .order_number_text{
        font-size: 0.8rem;
    }
    .email_text{
        font-size: 0.7rem;
    }
}
