:root{
	--mainGreen :#027782;
	--mainYellow :#ffcb08;
	--mainDark: #242325;
  	--mainWhite: #fff;
  	--mainGrey: #eeeeee;
}
.title-main{
    color: var(--mainGreen);
    text-transform: uppercase !important;
    font-size: 3.5rem;
}
.blockquote p{
    color: var(--mainGreen);
    border-left: 0.3rem solid var(--mainYellow);
}
.blockquote-footer{
    color: var(--mainGreen);
}
.main-text{
    font-size: 16px;
}
.img-container{
	position: relative;
}
.img-container::before{
	content: " ";
	position: absolute;
	top: -1.4rem;
	left: 1.6rem;
	width: 100%;
	height: 100%;
	outline: 0.5rem solid #027782;
	z-index: -1;
	transition: all 0.7s linear;
}
.img-container:hover:before{
	top: 0;
	left: 0;
}