.sidenav {
    height: 100%; /* 100% Full-height */
    width: 0px; /* 0 width - change this with JavaScript */
    position: fixed; /* Stay in place */
    z-index: 100; /* Stay on top */
    top: 0; /* Stay at the top */
    left: 0;
    background-color:white; /* Black*/
    overflow-x: hidden; /* Disable horizontal scroll */
    padding-top: 40px; /* Place content 60px from the top */
    transition:all 0.5s ease; /* 0.5 second transition effect to slide in the sidenav */
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.4);
}
.open-sidenav{
    width: 250px;
}
  
  .sidenav a{
    padding: 8px 8px 8px 12px;
    text-decoration: none;
    font-size: 0.9rem;
    color: #818181;
    display: block;
  }
  
  .side-log{
    padding: 8px 8px 8px 12px;
    text-decoration: none;
    font-size: 0.9rem;
    color: #818181;
    display: block;
    transition: 0.3s;
  }
  .side-user{
    padding: 8px 8px 8px 12px;
    text-decoration: none;
    font-size: 0.9rem;
    color: #818181;
    display: block;
    transition: 0.3s;
  }
  
  .sidenav a:hover {
    color: #027782;
  }
  .side-login:focus{
      outline: none;
  }
  
  .sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
    cursor:pointer ;
  }

  .side-login{
      border: 1px solid black;
      border-radius: 2rem;
      background-color: transparent;
      margin-left: 12px;
      width: 90%;
  }
  .nav-mob-active{
    color: #027782 !important;
  }
  
  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }